.navbar{
    background-color: rgb(red, green, blue);
    height:5rem;
    width:100vw;
    max-width: 75rem;
    margin:0 auto;
    padding:1rem 2rem;
    display: flex;
    align-items:center;
    justify-content: center;
    position:sticky;
    justify-content: space-between;
    top:0;
    z-index: 3;

}
.desktopMenuListItem{
    margin:1rem;
    cursor:pointer;
}
.desktopMenuListItem:hover{
    color:yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.desktopMenuBtn{
    background:white;
    color:black;
    border:none;
    display:flex;
    align-items: center;
    justify-content: center;
    padding:0 1rem;
    border-radius:2rem;
}
.desktopMenuImg{
    object-fit:cover;
    height:1rem;
    width:1rem;
    margin:1rem;

}
.contact-me-link {
    display: flex;
    align-items: center;
}

